import React, { useState } from "react";
import "./RegistrationForm.css";
import Button from "../Buttons/Button";

const RegistrationForm = () => {
  const [enteredName, setEnteredName] = useState("");
  const [enteredSurname, setEnteredSurname] = useState("");
  const [enteredDOB, setEnteredDOB] = useState("");
  const [enteredEmail, setEnteredEmail] = useState("");

  const nameChangeHandler = (event) => {
    setEnteredName(event.target.value);
  };

  const surnameChangeHandler = (event) => {
    setEnteredSurname(event.target.value);
  };

  const DOBChangeHandler = (event) => {
    setEnteredDOB(event.target.value);
  };

  const emailChangeHandler = (event) => {
    setEnteredEmail(event.target.value);
  };

  const formSubmitHandler = (event) => {
    event.preventDefault();

    const submittedFormData = {
      name: enteredName,
      surname: enteredSurname,
      dob: enteredDOB,
      enmail: enteredEmail,
    };

    console.log(submittedFormData);
    alert("you have now signed up");
  };

  return (
    <div className="reg-form__container">
      <form className="reg-form" onSubmit={formSubmitHandler}>
        <div className="reg-form__input">
          <label>Name</label>
          <input type="text" onChange={nameChangeHandler} />
        </div>
        <div className="reg-form__input">
          <label>Surname</label>
          <input type="text" onChange={surnameChangeHandler} />
        </div>
        <div className="reg-form__input">
          <label>Date of Birth</label>
          <input
            type="date"
            min="1900-01-01"
            max="2016-01-01"
            onChange={DOBChangeHandler}
          />
        </div>
        <div className="reg-form__input">
          <label>Email Address</label>
          <input type="email" onChange={emailChangeHandler} />
        </div>
        <div className="button-submit">
          <Button type="submit" />
        </div>
      </form>
    </div>
  );
};

export default RegistrationForm;
